.home-banner {
	content: '';
	width: 100%;
	height: 442px;
	position: relative;
}

.home-banner .bg {
	background: url(/static/media/bg_3_blur.c1486126.jpg) bottom left;
	background-size: 100% auto;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: -2;
}

.home-banner .overlay {
	background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAADCAYAAABWKLW/AAAABGdBTUEAAK/INwWK6QAAABl0RVh0U29mdHdhcmUAQWRvYmUgSW1hZ2VSZWFkeXHJZTwAAAAdSURBVHjaYmBgYPD9//8/AwgzgggYYGJAAgABBgAL7wlIbK4OiwAAAABJRU5ErkJggg==);
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: -1;
}

.home-banner .container {
	margin: auto 0;
	color: #fff;
}
.home-banner .container h1 span {
	color: #9FF569;
}
.home-banner .container .btn {
	border-radius: 100px;
	position: relative;
}
.home-banner .container .btn:hover:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAADCAYAAABWKLW/AAAABGdBTUEAAK/INwWK6QAAABl0RVh0U29mdHdhcmUAQWRvYmUgSW1hZ2VSZWFkeXHJZTwAAAAVSURBVHjaYmBgYPBlwAJ8URgAAQYAEGUA6KfeHS0AAAAASUVORK5CYII=);
	border-radius: 100px;
}
.hire-me {
    background-image: url(/static/media/bg_1.f7de43c6.jpg);
    /*background: #3e64ff;*/
    z-index: 0;
    position: relative;
    padding: 120px;
    margin: 0;
    color: #fff;
}
.hire-me h2 > span {
	color: #9FF569;
}
.hire-me .overlay {
	position: absolute;
	content: '';
	background: #3e64ff;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	opacity: .7;
}
.hire-me a.btn {
	border-radius: 100px;
}
.block-20 {
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    position: relative;
    display: block;
    width: 100%;
    height: 300px;
}
.blog-entry {
    position: relative;
    height: 100%;
}
@media (min-width: 768px) {
    .blog-entry {
        margin-bottom: 30px;
    }
}
@media (max-width: 767.98px) {
    .blog-entry {
        margin-bottom: 30px;
    }
}
.blog-entry .text {
    width: 100%;
    margin: 0 auto;
}
.blog-entry .text .heading {
    font-size: 20px;
    margin-bottom: 16px;
    font-weight: 500;
}
.blog-entry .text .heading a {
    color: #000000;
    text-decoration: none;
}
.blog-entry .text .heading a:hover, .blog-entry .text .heading a:focus, .blog-entry .text .heading a:active {
    color: #3e64ff;
}
.blog-entry .meta {
    display: inline-block;
    margin-right: 5px;
    margin-bottom: 0;
    font-size: 16px;
    font-weight: 600;
}
.blog-entry .meta span {
    color: #3e64ff;
}
.blog-entry .meta a {
    color: #3e64ff;
}
.blog-entry .meta a:hover {
    color: #3e64ff;
}
.contact-box {
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 4px;
}
.contact-box .icon {
    width: 100px;
    height: 100px;
    background: #3e64ff;
    margin: 0 auto;
    margin-bottom: 10px;
    border-radius: 50%;
}
.contact-box .icon span {
    color: #fff;
}
.contact-box h3 {
    font-weight: 600;
    color: #000;
}
.card.service {
    height: 100%;
}
.card.service h5.card-title {
    position: relative;
    margin-bottom: 30px;
}
.card.service h5.card-title:after {
    position: absolute;
    bottom: -15px;
    left: 0;
    right: 0;
    content: '';
    width: 30px;
    height: 1px;
    background: #3364FF;
    margin: 0 auto;
}
.card.service .icon {
    display: block;
    margin-bottom: 30px;
}
.card.service .icon i {
    line-height: 1.0;
    color: #3e64ff;
}
.card.service .card-text {
    color: #777;
}
.card.counter-wrap .card-body h3.number {
    color: #3e64ff;
}
.card.counter-wrap .card-body span {
    color: #000000;
}


.about {
	box-sizing: border-box;
	box-sizing: border-box;
	box-shadow:0 6px 12px rgba(0,0,0,.175);
}
.about .img {
	border-radius: 8px;
}

.about .btn {
	border-radius: 32px;
}
